import React from 'react';
import { BrowserRouter, Switch, Route, Link } from 'react-router-dom';
import { ContactMe, AboutMe, SideBar, Pages } from './components';

const App = () => {
  return (
    <div>
      <BrowserRouter>
        <SideBar />
        <Switch>
          {Pages.map((page) => {
            return <Route exact path={page.link} component={page.component} />;
          })}
        </Switch>
      </BrowserRouter>
    </div>
  );
};

export default App;
